import React from 'react'
import { Link } from 'react-router-dom'
export default function MoviePreview({movie,handleAddToList,section}) {


    return (
        <React.Fragment>
        {/* <button onClick={()=>handleAddToList(movie)}>ADD</button> */}
        {/* <Link to={`/movie/${movie.id}`}> */}
        <div  className='card'  onClick={()=>handleAddToList(movie)}>
            <img className={section==='header'? 'main-img':'card-img'} src={`https://image.tmdb.org/t/p/w500/${movie.poster_path}`} alt=""/>
            {/* <h5 className='card-text'>{movie.title}</h5> */}
        </div>
        {/* </Link> */}
        </React.Fragment>
    )
}
