import React, { Component, useState, useEffect } from 'react'
import { shallowEqual, useSelector,useDispatch } from 'react-redux'

import movieService from '../services/movieService'
import MovieList from '../cmps/MovieList'
import MovieDetails from './MovieDetails'
import Modal from '../cmps/Modal'






export default function MoviePage() {

    const [movies, setMovies] = useState([])
    const [category, setCatgory] = useState('trending')
    const [page, setPage] = useState(1)
    const [selectedMovie,setSelectedMovie]=useState(null)
    const [ModalStatus,setModalStatus]=useState(false)
    const [movie,setmovie]=useState('')

    useEffect(() => {
        loadMovies()
        
      
        return () => {

        }
    }, [category,page])


    const myList = useSelector(state => state.myList.list)
    const dispatch = useDispatch()



    async function loadMovies() {
        const moviesToRender = await movieService.getMovies(category,page)
        if (page===1)setMovies(moviesToRender)
        else setMovies([...movies,...moviesToRender])

    }


    async function handleChange(ev) {
        setCatgory(ev.target.value)
        setPage(1)
        // setMovies(movies.results)
        // loadMovies()

    }

     async function handlePageChnage(diff){
         if (page===1 && diff===-1) return
         setPage(page+diff)
        // loadMovies()
    }

    async function handleAddToList(movie) {
        setmovie(movie)
        setSelectedMovie(movie)
        const trailer = await movieService.getTrailer(movie.id)
        movie.trailer=trailer.results[0].key||null
        showModal()
        
        //     console.log(trailer);
        //     movie.trailer = trailer.results[0].key
        //     setMovie(movie)

    }

   function showModal () {
    setModalStatus(true)
      };
    
      function  hideModal () {
        setModalStatus(false)
        setSelectedMovie('')
      };




    return (
        
        <React.Fragment>
           
              <Modal show={ModalStatus} handleClose={hideModal}>
              <div className="movie-details">
           {selectedMovie && <MovieDetails  movie={selectedMovie}></MovieDetails> 
           }
           </div>
             </Modal>
            <div className="main flex column" id="movies" name="section1">
            <div className="category-select flex space-around">
           {/* {category!=='trending' && <button onClick={()=>handlePageChnage(-1)}>Previous</button>} */}
            <select className="select"  onChange={handleChange} name="" id="">
                <option value="trending">trending</option>
                <option value="top_rated">Top Rated</option>
                <option value="popular">Popular</option>
                <option value="now_playing">Now Playing</option>
            </select>
            </div>
            <MovieList section='main'  handleAddToList={handleAddToList} movies={movies}></MovieList>
            {category!=='trending' && <button className="load-more" onClick={()=>handlePageChnage(1)}>Load More</button>}
        </div>
      
           </React.Fragment>
    )

}

