import React, { Component, useState, useEffect } from 'react'
import NavBar from './NavBar'
import movieService from '../services/movieService'
import MoviePreview from './MoviePreview'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import MovieDetails from '../pages/MovieDetails'
import Modal from '../cmps/Modal'

export default function Header() {



    const [movies, setMovies] = useState([])
    const [selectedMovie,setSelectedMovie]=useState(null)
    const [movie,setmovie]=useState('')
    const [ModalStatus,setModalStatus]=useState(false)

    useEffect(() => {
        loadMovies()

        return () => {

        }
    }, [])

    async function loadMovies() {
        const movies = await movieService.getMovies('popular', 1)
        movies.splice(6)
        setMovies(movies)

    }

    async function handleAddToList(movie) {
        setmovie(movie)
        setSelectedMovie(movie)
        const trailer = await movieService.getTrailer(movie.id)
        movie.trailer=trailer.results[0].key||null
        showModal()
        
        //     console.log(trailer);
        //     movie.trailer = trailer.results[0].key
        //     setMovie(movie)

    }

    function showModal () {
        setModalStatus(true)
          };
        
          function  hideModal () {
            setModalStatus(false)
            setSelectedMovie('')
          };
    

    const settings = {
        dots: true,
        infinite: true,
        speed: 1000,
        slidesToShow: 3,
        slidesToScroll: 3,
        autoplay:true ,
        autoplaySpeed:4000,
        arrows: false,
        // fade: true,
    }

    return (
        
        <div className="header">
            <NavBar></NavBar>
            <Modal show={ModalStatus} handleClose={hideModal}>
              <div className="movie-details">
           {selectedMovie && <MovieDetails  movie={selectedMovie}></MovieDetails> 
           }
           </div>
             </Modal>
            <div className="carousel-container">   {movies.length > 0 &&
                <Slider {...settings} >
                    {movies.map(movie =>
                        <MoviePreview section='header' handleAddToList={handleAddToList}  key={movie.id} movie={movie}></MoviePreview>
                    )}
                </Slider>
            } </div>
        </div>
    )
}
