import Axios from 'axios'

var axios = Axios.create({
    withCredentials: true,
    headers: {
        'Access-Control-Allow-Origin': '*',
    }
});

export default {
    getMovies,
    getMovieById,
    getTrailer,
    searchMovieByName
}


async function getMovies(criteria="top_rated",page=''){
 
  const {data}=(criteria==="trending")?   await  Axios.get(`https://api.themoviedb.org/3/trending/movie/day?api_key=fad5ea5be63eed1a347cf10919addc14`):
  await  Axios.get(`https://api.themoviedb.org/3/movie/${criteria}?api_key=fad5ea5be63eed1a347cf10919addc14&language=en-US&page=${page}`)
  let movies=data.results

  return [...movies]
}

async function searchMovieByName(movie){
    
    const {data}=await  Axios.get(`https://api.themoviedb.org/3/search/movie?api_key=fad5ea5be63eed1a347cf10919addc14&query=${movie}`)
  return data
}
async function getMovieById(movieId){
    
    const {data}=await  Axios.get(`https://api.themoviedb.org/3/movie/${movieId}?api_key=fad5ea5be63eed1a347cf10919addc14&language=en-US`)
  return data
}


async function getTrailer(movieId){

    const {data}=await  Axios.get(`https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=fad5ea5be63eed1a347cf10919addc14&language=en-US`)
    return data


}



