const INITIAL_STATE = {
list:[],

};

export default function MovieReducer(state = INITIAL_STATE, action) {

    
    switch (action.type) {
        
        case 'ADD_MOVIE':
                console.log(action);
                
              return { ...state, list: [...state.list,action.movie] }
        case 'INC_CART':
                
                return {...state,itemsInCart:state.itemsInCart+1}
        case 'DEC_CART':
                return {...state,itemsInCart:state.itemsInCart-1}
        case 'CLEAR_CART':
                return {...state,itemsInCart:0}
        default:
            return state; 
    }

}

